body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
}

.titles{
  height: 10vh;
  background-color: navy;
  color: white;
  line-height: 10vh;
  font-size: 20pt;
}

.listoflists{
  background-color: white;
}

.home{
  color: navy;
  font-family: Arial, Helvetica, sans-serif;
}

nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: lightcoral;
  color: white;
}

.login{
  color: navy;
  width: 75vh;
  text-align: center;
  margin: auto;
  height: 75vh;
}

.showlistsTitle{
  background-color: navy;
  height: 10vh;
  line-height: 10vh;
  color: white;
  text-align: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

.addlisttitle{
  height: 10vh;
  background-color: navy;
  color: white;
  line-height: 10vh;
  font-size: 20pt;
}

.addlist{
  color: navy;
}

.newlistinput{
  color: navy;
}

.showlists{
  list-style-type: none;
  color: navy;
  align-items: center;
}


.title{
  background-color: navy;
  color: white;
  min-height: 10vh;
  text-align: center;
  line-height: 10vh;
  font-style: italic;
  font-size: 30pt;
}

.additemtitle{
  color: white;
  font-size: 20pt;
}

.showItems{
  list-style-type: square;
  width: 50vh;
  background-image: url(data:image/png;base64,);
  color: black;
  background-position: center;
  margin: auto;
  padding-bottom: 10%;
  padding-top: 5%;
  text-align: left;
  padding-left: 5%;
  font-family: cursive;
  font-style: italic; 
  font-size: 15pt;
  min-height: 50vh;
  background-size: 300px;
  size: 20pt;
 
}

.addItem{
  width: 75vh;
  border-color: navy;
  border-style: solid;
  border-width: 4pt;
  border-style: dashed;
  background-color: lightcoral;
  color: white;
  margin: auto;
}

.addItemButton{
  background-color: navy;
  border: none;
  color: white;
  size: 45pt;
  height: 5vh;
  width: 7vh;
}

.listsButton{
  background-color: navy;
  border-color: white;
  color: white;
  height: 8vh;
  width: 40vh;
  font-size: 15pt;
}

.select{
 color: navy;
}

.deletebutton{
  background-color: navy;
  
  color: white;
  padding: 32 px;
  height: 10vh;
  width: 25vh;
  font-size: 17pt;
}

.nav-links{
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}




